<template>
   <f7-page name="about">
      <f7-navbar title="SETTINGS" back-link="Back"></f7-navbar>
      <f7-list style="margin:0">
         <f7-list-item>
            <span>Vibrate</span>
            <f7-toggle @change="changeVibrate" :checked="vibrate"></f7-toggle>
         </f7-list-item>
         <f7-list-item title="Sound" smart-select :smart-select-params="{openIn: 'popup'}">
            <select name="sound" v-on:change="changeSoundHandler">
               <option value="s1" :selected="sound == 's1'">Sound 1</option>
               <option value="s2" :selected="sound == 's2'">Sound 2</option>
               <option value="s3" :selected="sound == 's3'">Sound 3</option>
            </select>
         </f7-list-item>
         <f7-list-item title="Background" smart-select :smart-select-params="{openIn: 'popup'}">
            <select name="background" v-on:change="changeColorHandler">
               <option value="Red" :selected="setting_color == 'Red'">Red</option>
               <option value="White" :selected="setting_color == 'White'">White</option>
            </select>
         </f7-list-item>
         <f7-list-item>
            <f7-link class="color-theme-white" href="/about/" style="width:100%;justify-content: left;">Help</f7-link>
         </f7-list-item>
         <f7-list-item>
            <f7-link class="color-theme-white" @click="exitApp" style="width:100%;justify-content: left;">Exit App</f7-link>
         </f7-list-item>
      </f7-list>
   </f7-page>
</template>

<script>

export default {
  mounted() {
  },
  methods: {
    changeVibrate() {
      if (setting_vibrate) {
        setting_vibrate = false;
      } else {
        setting_vibrate = true;
      }
      this.vibrate = setting_vibrate;
    },
    changeColorHandler(e) {
      WHIP.changeColor(e.target.value);
    },
    changeSoundHandler(e) {
      WHIP.changeSound(e.target.value);
    },
    exitApp() {
      window.navigator.app.exitApp();
    }
  },
  data() {
    return {
      setting_color: setting_color,
      vibrate: setting_vibrate,
      sound: setting_sound
    }
  }
}
</script>
