<template>
   <f7-page name="home" :style="'background-color:'+ background_color">
      <!-- Top Navbar -->
      <f7-navbar :sliding="false">
         <f7-nav-title sliding>WHIPLASH</f7-nav-title>
         <f7-nav-right>
            <f7-link icon-ios="f7:menu" icon-aurora="f7:menu" icon-md="material:menu" href="/setting/"></f7-link>
         </f7-nav-right>
      </f7-navbar>
      <f7-block style="padding: 0;margin: 0;">
         <audio id="whipAudio1s1" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip.mp3" />
         </audio>
         <audio id="whipAudio2s1" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip.mp3" />
         </audio>
         <audio id="whipAudio3s1" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip.mp3" />
         </audio>
         <audio id="whipAudio1s2" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_2.mp3" />
         </audio>
         <audio id="whipAudio2s2" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_2.mp3" />
         </audio>
         <audio id="whipAudio3s2" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_2.mp3" />
         </audio>
         <audio id="whipAudio1s3" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_3.mp3" />
         </audio>
         <audio id="whipAudio2s3" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_3.mp3" />
         </audio>
         <audio id="whipAudio3s3" controls="controls" width="100%" controlsList="nodownload" style="display:none">
            <source src="static/whip_3.mp3" />
         </audio>
         <img :src="background_image" @click="playAudio" width="100%"/>
      </f7-block>
      <f7-sheet class="ads-sheet" :opened="sheetOpened" @sheet:closed="sheetOpened = false" style="height:auto" swipe-to-close backdrop>
        <f7-page-content>
          <f7-block-title large>{{ads_title}}</f7-block-title>
          <f7-block>
            <div v-html="ads_content"></div>
          </f7-block>
        </f7-page-content>
      </f7-sheet>
   </f7-page>
</template>

<script>

global.WHIP = true;
global.setting_vibrate = true;
global.setting_color = "Red";
global.setting_sound = "s1";

export default {
  mounted() {
    WHIP = this;
    if (typeof shake !== "undefined") {
      shake.startWatch(this.onShake, 40);
    }
    this.reMedia(setting_sound);
    if (setting_vibrate) {
      setting_vibrate = true;
    }
    // ads
    /*
    {
      "title": "Ads",
      "content": "Ads Content"
    }
    */
    var self = this;
    setTimeout(function () {
      if (typeof window.navigator !== "undefined" && window.navigator.onLine) {
        fetch("https://wenangsani.github.io/static-cdn/bd/ads.json").then(res => res.text()).then(data => {
          data = JSON.parse(data);
          if (typeof data.content !== "undefined") {
            self.ads_title   = data.title;
            self.ads_content = data.content;
            self.sheetOpened = true;
          }
        });
      }
    }, 1000);
  },
  methods: {
    playAudio() {
      var now = this.now + 1;
      if (this.now >= 3) {
        var now = 1;
      }
      this.media[now].play();
      this.now = now;
      if (setting_vibrate) {
        window.navigator.vibrate(350);
      }
    },
    onShake() {
      this.playAudio();
    },
    changeColor(type) {
      switch (type) {
        case "Red":
          this.background_color = "#ff091c";
          this.background_image = "static/BDSM_red.jpg";
        break;
        case "White":
          this.background_color = "#ffffff";
          this.background_image = "static/BDSM.jpg";
        break;
      }
      setting_color = type;
    },
    changeSound(type) {
      setting_sound = type;
      this.reMedia(type);
    },
    reMedia(s) {
      this.media = [];
      this.media[1] = document.getElementById("whipAudio1" + s);
      this.media[2] = document.getElementById("whipAudio2" + s);
      this.media[3] = document.getElementById("whipAudio3" + s);
    }
  },
  data() {
    return {
      now: 0,
      media: [],
      background_color: "#ff091c",
      background_image: "static/BDSM_red.jpg",
      sheetOpened: false,
      ads_title: "",
      ads_content: ""
    }
  }
}
</script>